import { useQuery } from 'react-query';
import { getCoinsList } from '../apis/coins';
import { SearchParams } from '../types';

// Hook
export const useGetCoinsList = (params: SearchParams = {}) => {
  return useQuery(
    ['coins', params],
    () => getCoinsList(params),
    {
      refetchInterval: 30000, // 30
      staleTime: 25000, // 25
      cacheTime: 5 * 60 * 1000, // 5
    }
  );
};