import api from "../utils/axios";
import { CoinsResponse, SearchParams } from "../types";

// 
export async function getCoinsList(
  params: SearchParams = {}
): Promise<CoinsResponse> {
  try {
    const response = await api.get("/coins/list", { params });
    return response.data;
  } catch (error) {
    console.error(":", error);
    throw error;
  }
}