import React, { useState, useEffect } from 'react';
import { Thumbnail, ThumbnailProps } from '@shopify/polaris';
import CoinDefaultImg from '../../assets/images/coin_default_img.png';

//  ThumbnailProps
interface FallbackThumbnailProps extends Omit<ThumbnailProps, 'source' | 'alt'> {
  /**  */
  src: string;
  /**  */
  alt: string;
  /**  */
  fallbackSrc?: string;
  /**  */
  fallbackAlt?: string;
  /**  */
  size?: 'extraSmall'| 'small' | 'medium' | 'large';
}

/**
 * 
 * 
 */
export const FallbackThumbnail: React.FC<FallbackThumbnailProps> = ({
  src,
  alt,
  fallbackSrc = CoinDefaultImg,
  fallbackAlt,
  size = 'medium',
  ...thumbnailProps
}) => {
  const [imgLoadError, setImgLoadError] = useState<boolean>(false);

  useEffect(() => {
    //  src 
    setImgLoadError(false);

    // 
    const img = new Image();
    
    const handleLoad = () => {
      setImgLoadError(false);
    };

    const handleError = () => {
      setImgLoadError(true);
    };

    img.onload = handleLoad;
    img.onerror = handleError;
    img.src = src;

    // 
    return () => {
      img.onload = null;
      img.onerror = null;
    };
  }, [src]);

  // 
  const finalSrc = imgLoadError ? fallbackSrc : src;
  const finalAlt = imgLoadError ? (fallbackAlt || ` - ${alt}`) : alt;

  return (
    <Thumbnail
      source={finalSrc}
      alt={finalAlt}
      size={size}
      {...thumbnailProps}
    />
  );
};

export default FallbackThumbnail;